.sidebar-wrap {
	right: 0;
	position: fixed;
	top: 0px;
	bottom: 0px;
	z-index: 1000;
	height: 100%;
	transition: width 0.5s ease;
	-moz-transition: width 0.5s ease;
	-ms-transition: width 0.5s ease;
	-webkit-transition: width 0.5s ease;
	-o-transition: width 0.5s ease;
	width: 0;
	overflow: hidden;
}
.c-navbar {
position: relative;
height: 50px;
}
.c-navbar .header {
	height: 50px;
    display: flex;
    justify-content: center;
	align-content: center;
    margin-top: 15px;

}
 .title{
	font-size: 17px;
} 

.hamburger{
	position: absolute;
	right: 17px;
}
.hamburger > div{
		height:4px;
		width: 25px;
		background-color: #5d5d5d;
		margin: 4px;
}
.sidebar-open .sidebar-wrap {
	width: 75%;

}
.sidebar-background {
	background-color: rgba(0,0,0,0.4);
	width: 0;
	height: 0;
	right: 0;
	top: 0;
	position: fixed;
	z-index: 999;
	opacity: 0;
	
	transition: opacity 0.5s ease;
	-moz-transition: opacity 0.5s ease;
	-ms-transition: opacity 0.5s ease;
	-webkit-transition: opacity 0.5s ease;
	-o-transition: opacity 0.5s ease;
}
.sidebar-open .sidebar-background
{
	opacity: 0.5;
	width: 100%;
	height: 100%;
}
.x-span {
	position:absolute;
	height: 15.3px;
	width: 15.8px;
	font-size: 37px;
	color: #ffffff;
	top: 0%;
    left: 5%;
}
.sidebar-header .x-span:hover {
	background-color: black;
  }
.sidebar {
	background-color: #FAFAFA;
	height: 100%;
	width: 100%;
}
.sidebar-header {
	background-color: #1b166e;
	width: 100%;
	height: 20%;
	display:  block;
	text-align:  center;
	padding: 5%;
	box-sizing:  border-box;
	position:  relative;
}
.appleseeds-logo {
	width: 78%;
	height: 54%;
	position: absolute;
	left: 40px;
	right: 0;
	top: 0;
	bottom:  0;
	margin: auto;
}
.profile-preview{
	height: calc(8vh + 10px);
	padding: 2vh;
	border-bottom: 1px solid #5d5d5d;
	margin-top: 1.5vh;
}
.profile-image{
	width: 7vh;
	height: 7vh;
    display: inline-block;
    vertical-align: top;
	border-radius: 50%;
    margin-bottom: 0px;
	bottom: 8px;
	left:4px;
	font-weight: 250;

    /* background-position: center; */
	transform: Scale(0, 0);
	-moz-transform: Scale(0, 0);
	-ms-transform: Scale(0, 0);
	-webkit-transform: Scale(0, 0);
	-o-transform: Scale(0, 0);
	transition: transform 0.5s ease;
	-moz-transition: -moz-transform 0.5s ease;
	-ms-transition: -ms-transform 0.5s ease;
	-webkit-transition: -webkit-transform 0.5s ease;
	-o-transition: -o-transform 0.5s ease;
	position:relative;
	z-index: 2;
}

.user-name {
	font-size:30px;
	font-family: Assistant-SemiBold;
	font-weight: 250;
	/* line-height: 11sp; */
}
.sidebar-open .profile-image{
	transform: Scale(1, 1);
	-moz-transform: Scale(1, 1);
	-ms-transform: Scale(1, 1);
	-webkit-transform: Scale(1, 1);
	-o-transform: Scale(1, 1);
}

.menu-item {
	padding: 2vh;
	height: 8vh;
	display:flex;
	border-bottom: 1px solid #ccc;
	align-items: center;
}

.menu-item > .icon {
	width: 25px;
}


.sidebar-options .menu-information{
	display: inline-block;
	transform: translateX(40vw);
	height: 6vh;
	/* font-weight: bold; */
	color: #2E598B;
	font-size: 22px;
	padding-right: 2vh;
	margin-right: 2vh;
    margin-top: auto;

	/* padding-top: 4vh; */

	transition: transform 0.5s ease;
	-moz-transition: -moz-transform 0.5s ease;
	-ms-transition: -ms-transform 0.5s ease;
	-webkit-transition: -webkit-transform 0.5s ease;
	-o-transition: -o-transform 0.5s ease;
}
.sidebar-options .menu-information:nth-child(2){
	transition-delay: 0.075s;
	-moz-transition-delay: 0.075s;
	-ms-transition-delay: 0.075s;
	-webkit-transition-delay: 0.075s;
	-o-transition-delay: 0.075s;
}
.sidebar-options .menu-information:nth-child(3){
	transition-delay: 0.15s;
	-moz-transition-delay: 0.15s;
	-ms-transition-delay: 0.15s;
	-webkit-transition-delay: 0.15s;
	-o-transition-delay: 0.15s;
}
.sidebar-options .menu-information:nth-child(4){
	transition-delay: 0.225s;
	-moz-transition-delay: 0.225s;
	-ms-transition-delay: 0.225s;
	-webkit-transition-delay: 0.225s;
	-o-transition-delay: 0.225s;
}
.sidebar-options .menu-information:nth-child(5){
	transition-delay: 0.3s;
	-moz-transition-delay: 0.3s;
	-ms-transition-delay: 0.3s;
	-webkit-transition-delay: 0.3s;
	-o-transition-delay: 0.3s;
}
.sidebar-open .sidebar-options .menu-information{
	transform: translateX(0);
	line-height: initial;
}
/* .sidebar-open .sidebar-options .menu-information:last-child{
	display: flex;
	align-items: flex-end;
} */
.sidebar-open .sidebar-options .menu-item:last-child{
	height: 15vh;
	display: flex;
	align-items: flex-end;
	
}

.sidebar-open .sidebar-options .menu-information .last{
	padding-top: 15px;
	/* vertical-align: middle; */
}

.name-wrap{
	display: inline-block;
	opacity:0;
	margin-right:-2.5em;
	transform: Scale(0, 0);
	-moz-transform: Scale(0, 0);
	-ms-transform: Scale(0, 0);
	-webkit-transform: Scale(0, 0);
	-o-transform: Scale(0, 0);
	transform-origin: 100% 50%;
	transition: margin 0.5s ease-out, opacity 0.5s ease, transform 0.5s ease;
	-moz-transition: margin 0.5s ease-out, opacity 0.5s ease, -moz-transform 0.5s ease;
	-ms-transition: margin 0.5s ease-out, opacity 0.5s ease, -ms-transform 0.5s ease;
	-webkit-transition: margin 0.5s ease-out, opacity 0.5s ease, -webkit-transform 0.5s ease;
	-o-transition: margin 0.5s ease-out, opacity 0.5s ease, -o-transform 0.5s ease;
	position:relative;
	z-index: 1;
}
.sidebar-open .name-wrap{
	opacity: 1;
	margin-right: 2vw;
	transform: Scale(1, 1);
	-moz-transform: Scale(1, 1);
	-ms-transform: Scale(1, 1);
	-webkit-transform: Scale(1, 1);
	-o-transform: Scale(1, 1);
}